
html {
    box-sizing: border-box;
    
}
*, *:before, *:after {
box-sizing: inherit;
}
main {
    display: block;
}
ul, 
li, 
dl, 
dd,
p,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
}
ul, ol {
    list-style: none;
    padding-left: 0;
  }
:link:not(:hover) {
    text-decoration: none;
}
:root {
    --header-size: 1.6rem;
    --header-margin-top: 0.15rem;
    --line-height__body: 1.35rem;
    --line-height__header: 2.7rem;
    --column: auto;
    --column-gap: 3em;
    --font__info: 'Theinhardt', sans-serif;
    --font__header: 'Theinhardt', sans-serif;
    --color__body: rgba(0,0,0,0.9);
    --color__subtle-off-white: #fafcfa;
    --color__ice: #4db0c4;
    --color__fire: #c47333;
    --color__prince: #c53fa6;
    --section-decoration: 0.075rem solid var(--color__ice);
    --rhythm__vertical: 1.35rem;
  }
@keyframes flip-out {
  0% {
    transform: rotateX(0deg);
  }
  100% {
    transform: rotateX(90deg);
    opacity: 0.4;
  }
}
@keyframes flip-in {
  0% {
    transform: rotateX(-90deg);
    opacity: 0.4;
  }
  100% {
    transform: rotateX(0deg);
  }
}
@keyframes spin-out {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(90deg);
    opacity: 0.4;
  }
}
@keyframes spin-in {
  0% {
    transform: rotateY(90deg) translateX(50%) rotateY(-90deg) translateX(50%)
      rotateY(90deg);
  }
  100% {
    transform: rotateY(0deg);
    transform-origin: 0 25%;
  }
}
@keyframes spinner {
  to {
    transform: rotate(-1turn);
  }
}
@font-face {
  font-family: "Calluna";
